<template>
  <div id="app">
    <div class="column is-12">
      <div class="card events-card">
        <header class="card-header">
          <p class="card-header-title">
            Cryptocurrency
          </p>
          <a href="#" class="card-header-icon" aria-label="more options">
            <span class="icon">
              <i class="fa fa-angle-down" aria-hidden="true"></i>
            </span>
          </a>
        </header>
        <div class="card-table">
          <div class="content">
            <table class="table is-fullwidth">
              <thead>
                <tr>
                  <th>Currency</th>
                  <th>Price</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody v-if="coins && coins.length">
                <tr v-for="coin of coins" :key="coin.id">
                  <td>{{coin.name}}</td>
                  <td>{{coin.price_usd}}</td>
                  <td>
                    <a v-on:click="showCoin(coin)" class="button is-small is-primary">
                      Show
                    </a>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>        
    </div>
    <mugen-scroll :handler="fetchData" :should-handle="!loading">
      loading...
    </mugen-scroll>
    <card-modal :visible="showModal" @cancel='close()' @ok='close()' :title="titleModal" transition="zoom">
      <div class="content has-text-centered">
        <p> {{ coinModal.name }} </p>
        <p>Percent change in 1h:  {{ coinModal.percent_change_1h }}</p> 
        <p>Percent change in 24h: {{ coinModal.percent_change_24h }}</p> 
        <p>Percent change in 7d: {{ coinModal.percent_change_7d }}</p> 
      </div>
    </card-modal>
  </div>
</template>

<script>
import { Modal, CardModal } from 'vue-bulma-modal'
import MugenScroll from 'vue-mugen-scroll'
import axios from 'axios';

export default {
  name: 'app',
  components: {
    Modal,
    CardModal,
    MugenScroll
  },
  data() {
    return {
      coins: [],
      errors: [],
      start: 1,
      limit: 10,
      loading: false,
      coinModal: [],
      showModal: false,
      titleModal: ''
    }
  },
  methods: {
    fetchData() {
      this.loading = true
      axios.get('https://api.coinmarketcap.com/v1/ticker/?start=' + this.start + '&limit=' + this.limit)
      .then(response => {
        this.start = this.limit + this.start
        // JSON responses are automatically parsed.
        response.data.map((coin) => {
          this.coins.push(coin)
          this.loading = false
        })
      })
      .catch(e => {
        this.errors.push(e)
      })
    },
    showCoin(coin) {
      this.showModal = true
      this.coinModal = coin
      this.title = coin.name
    },
    close () {
      this.showModal = false
    },
    ok () {
      this.showModal = false
    },
  },
  created() {
    
  }

}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



